import { Component, OnInit, Input } from '@angular/core';
import { LocalStorageService } from '.././local-storage.service';

@Component({
  selector: 'app-content',
  template: require('./content.component.html'),
  styles: [require('./content.component.scss')],
})
export class ContentComponent  {

  static chosenInitialized:boolean = false
  @Input()
  showData: Array<Object>;
  selectData: Array<Object>;
  ImgUrl: any;
  isShowImg: boolean;
  isShowInfo: boolean;
  constructor(private localstoragesvc: LocalStorageService) {
    this.isShowImg = false;
    this.isShowInfo = false;
    this.selectData = [{
      Name: '',
      Sex: '',
      Type: '',
      Build: '',
      Variety: '',
      Age: '',
      Reason: '',
      AcceptNum: '',
      ChipNum: '',
      IsSterilization: '',
      HairType: '',
      Note: '',
      Resettlement: '',
      Phone: '',
      Email: '',
      ChildreAnlong: '',
      AnimalAnlong: '',
      Bodyweight: '',
      ImageName: '',
    }];
  }

  showImg(url) {
    this.isShowImg = true;
    this.ImgUrl = url;
  }
  selectItem(data) {
    this.isShowInfo = true;
    this.selectData = data;
  }

  setFavorite(item: any) {
    this.localstoragesvc.setFavorite(item);
  }

}


// "Name(名稱)、Sex(性別)、Type(動物種類)、Build(體型)、 Age (年齡)、 Variety (品種)、 Reason (進所原因)、
// AcceptNum (收容編號)、 ChipNum (晶片號碼)、 IsSterilization (絕育否)、 HairType (毛色)、 Note (描述)、 Resettlement (位置)、
//  Phone(聯絡電話)、Email(聯絡email)、ChildreAnlong(可否與其他孩童相處)、 AnimalAnlong(可否與其他動物相處)、Bodyweight(體重)、
//  ImageName(圖片)"



